@mixin post-style($book: false){

  // section
  .wp-block-hametuha-section{
    padding-top: 80px;
    padding-bottom: 80px;
    @include mq-max( $screen-sm-max ) {
      padding-top: 40px;
      padding-bottom: 40px;
    }
  }

  // 共通要素
  .alignleft{
    float: left;
    margin: 1em 1em 1em 0;
  }
  .alignright{
    float: right;
    margin: 1em 0 1em 1em ;
  }
  .aligncenter{
    margin: 1em auto;
  }
  img{
    width: auto;
    height: auto;
    max-width: 100%;
    &.aligncenter{
      display: block;
    }
  }
  .wp-caption{
    max-width: 100%;
    &.alignnone{
      margin: 1em auto;
      text-align: center;
    }
  }
  .wp-caption-text{
    font-size: 0.8em;
    color: $text-muted;
  }
  aside{
    margin: 1em 0;
    padding: 1em;
    background: #f9f9f9;
    color: #000;
    font-size: 0.85em;
    h1, h2, h3, h4, h5, h6{
      &:first-child{
        margin-top: 0;
      }
    }
  }

  // 強調
  .text-emphasis{
    -webkit-text-emphasis-style: filled sesame ;
  }

  // 引用
  blockquote.wp-block-quote,
  .wp-block-pullquote blockquote{
    &.is-large{
      font-size: 2em;
    }
    color: #000;
    position: relative;
    border-left: none;
    padding: 1.5em;
    margin: 1.5em 0;
    p,cite{
      position: relative;
      z-index: 2;
    }
    &:before{
      content: '“';
      position: absolute;
      line-height: 1;
      top: 0;
      left: 0;
      color: $thin-border;
      font-size: 8em;
      z-index: 1;
    }

  }

  // 罫線
  hr{
    &.wp-block-separator{
      border-color: #000;
      margin-top: 80px;
      margin-bottom: 80px;
      @include mq-max( $screen-sm-max ){
        margin-top: 40px;
        margin-bottom: 40px;
      }
      width: 30%;
      max-width: 150px;
      &.is-style-wide,
      &.is-style-dots{
        width: 100%;
        max-width: none;
      }
    }
  }
  // Button
  .wp-block-button{
    a{
      transition: color .3s linear, background-color .3s linear, border-bottom-color .3s linear;
      border: 2px solid transparent;
      &:link, &:visited{
        text-decoration: none;
        color: #fff;
        border-color: #000;
        background-color: #000;
      }
      &:hover, &:active{
        text-decoration: none;
        color: #000;
        border-color: #000;
        background-color: transparent;
      }
    }
    &.is-style-outline{
      a{
        &:link, &:visited{
          color: #000;
          border-color: #000;
          background-color: transparent;
        }
        &:hover, &:active{
          color: #fff;
          border-color: #000;
          background-color: #000;
        }
      }
    }
    @each $setting in (
                    ( primary $brand-primary ),
                    ( success $brand-success ),
                    ( danger $brand-danger ),
                    ( warning $brand-warning )
    ) {
      $class-name: nth( $setting, 1 );
      $color: nth( $setting, 2 );
      &.#{$class-name}, &.is-style-squared.#{$class-name} {
        a.wp-block-button__link{
          &:link, &:visited {
            color: #fff;
            background-color: $color;
            border-color: $color;
          }
          &:hover, &:active {
            color: $color;
            background-color: transparent;;
            border-color: $color;
          }
        }
      }
      &.is-style-outline.#{$class-name}{
        a{
          &:link, &:visited{
            color: $color;;
            border-color: $color;;
            background-color: transparent;
          }
          &:hover, &:active{
            color: #fff;
            border-color: $color;;
            background-color: $color;;
          }
        }
      }
    }
  }

  .interviewer{
    color: #777;
    font-style: italic;
    font-family: $font-family-sans-serif;
  }
  .interviewee{
    font-weight: bold;
    color: #000;
  }

  // 普通の投稿
  @if( $book == false){
    //
    // 普通の投稿
    //
    // ------------------------
    //
    // リンク
    a:link, a:visited{
      text-decoration: underline;
    }
    // 文章
    p{
      margin: 1em 0;
      &:first-child{
        margin-top: 0;
      }
      &.has-drop-cap:first-letter{
        font-size: 3em;
        line-height: 1;
      }
    }
    // 見出し
    h1, h2, h3, h4, h5, h6{
      margin-top: 2em;
      margin-bottom: 1em;
      clear: both;
      &:first-child{
        margin-top: 0;
      }
    }
    h1{
      font-size: 3em;
      font-weight: normal;
      padding-bottom: 0.5em;
      position: relative;
      &:after{
        position: absolute;
        display: block;
        content: "";
        height: 3px;
        background-color: #000;
        bottom: 0;
        left: 0;
        width: 30%;
        max-width: 150px;
      }
      &[style*="center"]:after{
        left: 50%;
        transform: translateX( -50% );
      }
      &[style*="right"]:after{
        left: auto;
        right: 0;
      }
    }
    h2{
      font-size: 2em;
      font-weight: normal;
      border-top: 1px solid #000;
      border-bottom: 1px solid #000;
      padding: 0.5em;
    }
    h3{
      font-size: 1.6em;
      font-weight: bold;
    }
    h4{
      font-size: 1.6em;
      font-weight: normal;
      border-left: 5px solid #000;
      padding-left: 0.5em;
    }
    h5{
      font-size: 1.4em;
      font-weight: normal;
      border-bottom: 1px solid #000;
      padding-bottom: 0.5em;
    }
    h6{
      color: #000;
      font-size: 1em;
      font-weight: bold;
    }
    // 引用
    blockquote{
      font-style: italic;
      cite{
        display: block;
        text-align: right;
      }
    }
    cite, q{
      font-style: italic;
      &:before{
        content: '“';
      }
      &:after{
        content: '”';
      }
    }
    small{
      color: #666;
      font-size: 1em;
      &:before{
        content: '〔';
      }
      &:after{
        content: '〕';
      }
    }
    // テーブル
    table{
      font-size: 0.85em;
      @extend .table;
      @extend .table-striped;
    }
    // リスト
    ul, ol, dl{
      margin: 1em 0;
    }
    dl{
      $border:  2px solid $thin-border;
      dt{
        border-bottom: 1px solid $thin-border;
        border-left: $border;
        padding: 0.25em 0.5em 0.25em 15px;
        margin-top: 1em;
      }
      dd{
        border-left: $border;
        padding: 0.25em 0.5em 0.25em 15px;
        margin-bottom: 0.5em;
      }
      margin: 1em 0;
    }
    // インライン要素
    abbr, acronym{
      border-bottom: 3px dashed $thin-border;
      font-weight: bold;
      font-family: serif;
      cursor: help;
    }
    big{
      font-size: 2em;
    }
    del, strike{
      text-decoration: line-through;
      color: #aaa;
    }
    ins{
      background-color: #fdffbf;
      text-decoration: none;
    }
    em{
      font-style: normal;
      @include text-emphasis(sesame filled);
    }
    // iframe
    iframe{
      display: block;
      margin: 1em auto;
      max-width: 100%;
    }
    object, iframe{
      max-width: 100%;
    }
  }@else{
    //
    // 作品のスタイル
    //
    // ------------------------
    //


    & > *:first-child{
      margin-top: 0;
    }

    // 本文
    p{
      margin: 0;
      &.indent{
        text-indent: 1em;
      }
    }

    h1, h2, h3, h4, h5, h6{
      margin: 1.5em 0 0.5em;
      font-weight: normal;
      font-family: $work-bold;
      clear: both;
    }

    h1{
      font-size: 2em;
    }
    h2{
      color: $clear-font;
      font-size: 1.8em;
      padding-bottom: 0.5em;
      border-bottom: 3px double $clear-font
    }
    h3{
      font-size: 1.4em;
      color: $blue-font;
    }
    h4{
      font-size: 1.2em;
      color: $shade-font;
      padding-bottom: 0.5em;
      border-bottom: 1px dotted $thin-border;
    }
    h5{
      font-size: 1.2em;
    }
    h6{
      font-size: 1em;
    }

    // 引用
    blockquote{
      margin: 1em 0 1em 2em;
      color: #444;
      border-left: none;
      padding: 0;
      font-size: inherit;
      cite{
        display: block;
        font-size: 0.85em;
        text-align: right;
        &:before{
          content: '“';
        }
        &:after{
          content: '”';
        }
      }
      p cite{
        text-indent: -1em;
      }
    }
    q{
      &:before{
        content: "“";
      }
      &:after{
        content: "”";
      }
    }

    // コラム
    aside{
      font-family: $work-body-sans;
      h1, h2, h3, h4, h5, h6{
        font-family: $work-bold-sans;
      }
    }

    // テーブル
    table{
      width: 100%;
      table-layout: auto;
      border-collapse: collapse;
      margin: 1em 0;
      thead{
        th{
          color: $shade-font;
          border-bottom: 3px double $shade-font;
          font-family: sans-serif;
          font-weight: bold;
        }
      }
      th, td{
        border-bottom: 1px solid $shade-font;
        padding: 0.5em;
      }
      tbody{
        th{
          border-right: 1px dotted $shade-font;
        }
      }
    }

    // リスト
    ul, dl, ol{
      margin: 1em 0;
    }

    dl{
      dt{
        font-family: $work-bold;
        margin-top: 0.5em;
      }
      dd{
        margin-left: 1em;
        &:last-child{
          margin-bottom: 0.5em;
        }
      }
    }


    // その他
    acronym, abbr{
      cursor: help;
      border-bottom: 2px dashed $shade-font;
    }
    big{
      font-size: 4em;
    }
    cite{
      font-style: italic;
    }
    del, s, strike{
      color: $more-shade-font;
      text-decoration: line-through;
    }
    ins, small{
      color: $shade-font;
      text-decoration: none;
      &:before{
        content: '〔';
      }
      &:after{
        content: '〕';
      }
    }
    strong{
      font-family: $work-bold;
    }
  }
}
